<h2 id="yum安装" tabindex="-1"><a class="header-anchor" href="#yum安装" aria-hidden="true">#</a> Yum安装</h2>
<p>创建<code>/etc/yum.repos.d/nginx.repo</code></p>
<h2 id="yum-epel安装" tabindex="-1"><a class="header-anchor" href="#yum-epel安装" aria-hidden="true">#</a> Yum Epel安装</h2>
<p>安装<code>epel-releas</code></p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>yum <span class="token function">install</span> nginx
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="源码安装" tabindex="-1"><a class="header-anchor" href="#源码安装" aria-hidden="true">#</a> 源码安装</h2>
<p>检查nginx所需要的安装环境,提示需要什么就安装什么</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>./configure
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>默认安装在了 <strong>/usr/local/nginx</strong> 路径</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">make</span> <span class="token operator">&amp;&amp;</span> <span class="token function">make</span> <span class="token function">install</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="配置环境变量" tabindex="-1"><a class="header-anchor" href="#配置环境变量" aria-hidden="true">#</a> 配置环境变量</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">vim</span> /etc/profile
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">export</span> <span class="token assign-left variable">NGINX_HOME</span><span class="token operator">=</span>/usr/local/nginx

<span class="token builtin class-name">export</span> <span class="token assign-left variable"><span class="token environment constant">PATH</span></span><span class="token operator">=</span><span class="token variable">$NGINX_HOME</span>/sbin:<span class="token environment constant">$PATH</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">source</span> /etc/profile
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="常用命令" tabindex="-1"><a class="header-anchor" href="#常用命令" aria-hidden="true">#</a> 常用命令</h3>
<ol>
<li>测试配置文件
<code>nginx -t</code></li>
<li>重启
<code>nginx -s reopen</code></li>
<li>重新加载配置
<code>nginx -s reload</code></li>
<li>强制停止
<code>nginx -s stop</code></li>
<li>退出
<code>nginx -s quit</code></li>
<li>启动
<code>nginx</code></li>
</ol>
